import React, { PureComponent } from 'react'
import Form, { IProps } from './Form'
import FormGenerator from './FormGenerator'
import { STOCK_SUPPLIER } from '../../graphql/query/remote'
import { CREATE_STOCK_SUPPLIER, UPDATE_STOCK_SUPPLIER } from '../../graphql/mutation/remote'

export const defaults: IFormGenerator = {
  name: {
    type: 'text',
    default: '',
    required: true,
  },
  contact: {
    type: 'number',
    default: 0,
    max: 9999999999,
    validate: (value: number) => {
      if (value !== 0 && String(value).length !== 10) {
        return 'Invalid contact number'
      }
      return null
    },
  },
}

class CreateStockSupplier extends PureComponent<IProps> {
  render() {
    const { id } = this.props.match.params
    return (
      <Form
        {...this.props}
        query={STOCK_SUPPLIER}
        defaults={defaults}
      >
        {({ update, defaults, queryResult }: IFormProps) => {
          return (
            <FormGenerator
              {...this.props}
              defaults={defaults}
              mutation={update ? UPDATE_STOCK_SUPPLIER : CREATE_STOCK_SUPPLIER}
              title={!update ? 'Create a new Stock Supplier' : 'Update this Stock Supplier'}
              submitText={!update ? 'Create StockSupplier' : 'Update StockSupplier'}
              mutateFunc={(fn, data) => fn({ variables: { id, data } })}
            >
            </FormGenerator>
          )
        }}
      </Form>
    )
  }
}

export default CreateStockSupplier
